/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { useStaticQuery, graphql } from 'gatsby';
import Image from 'gatsby-image';
import { ImageSharp } from 'src/models';
import { Color } from 'src/constants';
import { Media } from 'src/utils';

interface QueryResult {
  logo: ImageSharp;
}

interface Props {
  size?: number;
}
const LubyconLogo = ({ size = 30, ...rest }: Props) => {
  const { logo }: QueryResult = useStaticQuery(graphql`
    query LogoQuery {
      logo: file(absolutePath: { regex: "/logo.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  return (
    <div
      css={css`
        display: flex;
        align-items: center;
        justify-content: center;
      `}
      {...rest}
    >
      <Image
        css={css`
          width: ${size}px;
          margin-right: 0.5rem;
        `}
        fluid={logo.childImageSharp.fluid}
        alt="Lubycon 로고"
      />
      <span
        css={css`
          color: ${Color.Black};
          font-size: ${size / 23}rem;
          ${Media.mobile} {
            display: none;
          }
        `}
      >
        LUBYCON
      </span>
    </div>
  );
};

export default LubyconLogo;
