/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { Link } from 'gatsby';
import { Row, Col } from 'react-bootstrap';
import { GlobalMenus, Color } from 'src/constants';
import { Media } from 'src/utils';
import Logo from 'components/Utils/LubyconLogo';
import { useAuth } from 'hooks/useAuth';
import RouteLink from 'components/Utils/RouteLink';

const Header = () => {
  const { isAuthorized } = useAuth();
  return (
    <header
      css={css`
        padding: 1rem;
      `}
    >
      <Row>
        <Col xs="auto">
          <Link
            css={css`
              display: flex;
              align-items: center;
              &:hover {
                text-decoration: none;
              }
            `}
            to="/"
          >
            <Logo />
          </Link>
        </Col>
        <Col />
        <Col xs="auto">
          <Menus>
            {GlobalMenus.map((menu, index) => (
              <MenuItem key={index}>
                <RouteLink to={menu.link}>{menu.name}</RouteLink>
              </MenuItem>
            ))}
            <MenuItem>
              {isAuthorized ? <RouteLink to="/logout">LOGOUT</RouteLink> : <RouteLink to="/login">LOGIN</RouteLink>}
            </MenuItem>
          </Menus>
        </Col>
      </Row>
    </header>
  );
};

const Menus = styled.ul`
  display: flex;
  padding: 0;
  margin: 0;
  align-items: center;
  height: 100%;
`;
const MenuItem = styled.li`
  font-size: 1rem;
  list-style: none;
  margin-right: 1rem;
  ${Media.mobile} {
    margin-right: 0.6rem;
  }
  a {
    color: ${Color.Grey900};
    text-decoration: none;
    transition: color 0.2s ease-in;
    &:hover {
      color: ${Color.Red500};
    }
    ${Media.mobile} {
      font-size: 0.7rem;
    }
  }
`;

export default Header;
