import firebase from 'gatsby-plugin-firebase';
import { useState, useEffect, useCallback } from 'react';

interface AuthScheme {
  isAuthorized: boolean;
  user: firebase.User;
  loginByEmail: (email: string, password: string) => Promise<firebase.auth.UserCredential>;
  logout: () => Promise<void>;
}
export function useAuth(): AuthScheme {
  const [currentUser, setCurrentUser] = useState(firebase.auth?.().currentUser);

  useEffect(() => {
    firebase.auth?.().onAuthStateChanged(user => {
      setCurrentUser(user);
    });
  }, []);

  const loginByEmail = useCallback((email: string, password: string) => {
    return firebase.auth?.().signInWithEmailAndPassword(email, password);
  }, []);

  const logout = useCallback(() => {
    return firebase.auth?.().signOut();
  }, []);

  return {
    isAuthorized: Boolean(currentUser),
    user: currentUser,
    loginByEmail,
    logout,
  };
}
